
            const initOGDF = require('../entry/rawogdf')
            const { PARAMETER_TYPE } = require('../utils/parameters')
            const OUR_PARAMETERS = {"useWorker":{"type":"BOOL","range":[true,false],"default":false}}
            const ORIGIN_PARAMETERS = {"settings":{"type":"CATEGORICAL","range":["standard","repulse","planar"],"default":"none"},"attractionWeight":{"type":"DOUBLE","range":[0,null],"default":100},"iterationNumberAsFactor":{"type":"BOOL","range":[true,false],"default":false},"nodeOverlapWeight":{"type":"DOUBLE","range":[0,null],"default":100},"numberOfIterations":{"type":"INT","range":[0,null],"default":0},"planarityWeight":{"type":"DOUBLE","range":[0,null],"default":500},"preferredEdgeLength":{"type":"DOUBLE","range":[0,null],"default":0},"preferredEdgeLengthMultiplier":{"type":"DOUBLE","range":[0,null],"default":2},"repulsionWeight":{"type":"DOUBLE","range":[0,null],"default":1000000},"speed":{"type":"CATEGORICAL","range":["Fast","Medium","HQ"],"default":"Medium"},"startTemperature":{"type":"INT","range":[0,null],"default":500}}
            const PARAMETERS = {
                ...OUR_PARAMETERS,
                ...ORIGIN_PARAMETERS,
            }
            function dh(graph, params, callback) {
                const ORIGIN_PARAMETER_SEQUENCE = ["settings","attractionWeight","iterationNumberAsFactor","nodeOverlapWeight","numberOfIterations","planarityWeight","preferredEdgeLength","preferredEdgeLengthMultiplier","repulsionWeight","speed","startTemperature"]
                let Module
                const parameters = {
                    ...{"useWorker":false,"settings":"none","attractionWeight":100,"iterationNumberAsFactor":false,"nodeOverlapWeight":100,"numberOfIterations":0,"planarityWeight":500,"preferredEdgeLength":0,"preferredEdgeLengthMultiplier":2,"repulsionWeight":1000000,"speed":"Medium","startTemperature":500},
                    ...params
                }
                const originalParameters = ORIGIN_PARAMETER_SEQUENCE.map((paramName) => {
                    if (PARAMETERS[paramName].type === PARAMETER_TYPE.CATEGORICAL) {
                        return PARAMETERS[paramName].range.indexOf(parameters[paramName])
                    } else {
                        return parameters[paramName]
                    }
                })
                const _graph = JSON.parse(JSON.stringify(graph))
                const graphAttributes = (function(graph){
                   let GRAPH_ATTRYBUTES = []
                    GRAPH_ATTRYBUTES[0] = (graph => graph.nodes.length)(graph)
                    GRAPH_ATTRYBUTES[1] = (graph => graph.links.length)(graph)
                       return GRAPH_ATTRIBUTES
                   })(_graph)
                const indexArrays = (function(graph){
                    let ATTRIBUTR_ARRAYS = [{"name":"source","type":{"length":4,"heap":"HEAP32"},"value":[],"setValue":"\n                    (graph) => {\n                        const linkAttributeArray = []\n                        for (let i = 0; i < GRAPH_ATTRIBUTES[1]; ++i) {\n                            linkAttributeArray.push((link => {\r\n                        const id2index = {}\r\n                        for (let i = 0; i < GRAPH_ATTRIBUTES[0]; ++i) {\r\n                            if (graph.nodes[i]['id'] in id2index) {\r\n                                throw Error('Duplicated Node ID')\r\n                            } else id2index[graph.nodes[i]['id']] = i\r\n                        }\r\n                        return id2index[link.source]\r\n                    })(graph.links[i]))\n                        }\n                        return linkAttributeArray\n                    }"},{"name":"target","type":{"length":4,"heap":"HEAP32"},"value":[],"setValue":"\n                    (graph) => {\n                        const linkAttributeArray = []\n                        for (let i = 0; i < GRAPH_ATTRIBUTES[1]; ++i) {\n                            linkAttributeArray.push((link => {\r\n                        const id2index = {}\r\n                        for (let i = 0; i < GRAPH_ATTRIBUTES[0]; ++i) {\r\n                            if (graph.nodes[i]['id'] in id2index) {\r\n                                throw Error('Duplicated Node ID')\r\n                            } else id2index[graph.nodes[i]['id']] = i\r\n                        }\r\n                        return id2index[link.target]\r\n                    })(graph.links[i]))\n                        }\n                        return linkAttributeArray\n                    }"}]
                    ATTRIBUTR_ARRAYS[0].value = (
                    (graph) => {
                        const linkAttributeArray = []
                        for (let i = 0; i < GRAPH_ATTRIBUTES[1]; ++i) {
                            linkAttributeArray.push((link => {
                        const id2index = {}
                        for (let i = 0; i < GRAPH_ATTRIBUTES[0]; ++i) {
                            if (graph.nodes[i]['id'] in id2index) {
                                throw Error('Duplicated Node ID')
                            } else id2index[graph.nodes[i]['id']] = i
                        }
                        return id2index[link.source]
                    })(graph.links[i]))
                        }
                        return linkAttributeArray
                    })(graph)
                    ATTRIBUTR_ARRAYS[1].value = (
                    (graph) => {
                        const linkAttributeArray = []
                        for (let i = 0; i < GRAPH_ATTRIBUTES[1]; ++i) {
                            linkAttributeArray.push((link => {
                        const id2index = {}
                        for (let i = 0; i < GRAPH_ATTRIBUTES[0]; ++i) {
                            if (graph.nodes[i]['id'] in id2index) {
                                throw Error('Duplicated Node ID')
                            } else id2index[graph.nodes[i]['id']] = i
                        }
                        return id2index[link.target]
                    })(graph.links[i]))
                        }
                        return linkAttributeArray
                    })(graph)
                       return ATTRIBUTR_ARRAYS
                   })(_graph)
                if (parameters.useWorker) {
                    const workerFunc = function () {
                        addEventListener('message', (e) => {
                            let { initOGDF, graphAttributes, indexArrays, originalParameters } = JSON.parse(e.data)
                            let evalstr = "initOGDF = " + initOGDF
                            eval(evalstr)
                            initOGDF().then(function (md) {
                                Module = md
                                const arrays = (
                                    function(arrays){
                                        let mallocs = []
                                        for (let name in arrays) {
                                            const array = arrays[name]
                                            const location = Module._malloc(array.type.length * array.value.length)
                                            for (let i = 0; i < array.value.length; ++i) {
                                                Module[array.type.heap][location / array.type.length + i] = array.value[i]
                                            }
                                            mallocs.push(location)
                                        }
                                        return mallocs
                                    }
            )(indexArrays)
                                const result = Module._DH(
                                    ...graphAttributes,
                                    ...arrays,
                                    ...originalParameters
                                )
                                const nodes = []
                                for (let i = 0; i < GRAPH_ATTRIBUTES[0]; ++i) {
                                    nodes[i] = {}
                                    nodes[i]['x'] = Module.HEAPF32[(result >> 2) + i * 2]
                                    nodes[i]['y'] = Module.HEAPF32[(result >> 2) + i * 2 + 1]
                                }
                                postMessage(JSON.stringify(nodes))
                                ;(
                                    function(arrays, buffers) {
                                        for (let array of arrays) {
                                            Module._free(array)
                                        }
                                        for (let buffer of buffers) {
                                            Module._free_buf(buffer)
                                        }
                                    })(arrays, [result])
                            })
                        })
                    }
                    const blob = new Blob(["(" + workerFunc.toString() + ")()"])
                    const url = window.URL.createObjectURL(blob)
                    const worker = new Worker(url)
                    worker.postMessage(
                        JSON.stringify({
                            initOGDF: initOGDF.toString(), // ! Maybe we can put initOGDF out of web worker
                            graphAttributes,
                            indexArrays,
                            originalParameters
                        })
                    )
                    worker.onmessage = function (e) {
                        const nodePositions = JSON.parse(e.data)
                        for (let i = 0; i < GRAPH_ATTRIBUTES[0]; ++i) {
                            _graph.nodes[i].x = nodePositions[i].x
                            _graph.nodes[i].y = nodePositions[i].y
                        }
                        worker.terminate()
                        callback(_graph)
                    }
                } else {
                    initOGDF().then(function (md) {
                        Module = md
                        const arrays = (
                                    function(arrays){
                                        let mallocs = []
                                        for (let name in arrays) {
                                            const array = arrays[name]
                                            const location = Module._malloc(array.type.length * array.value.length)
                                            for (let i = 0; i < array.value.length; ++i) {
                                                Module[array.type.heap][location / array.type.length + i] = array.value[i]
                                            }
                                            mallocs.push(location)
                                        }
                                        return mallocs
                                    }
            )(indexArrays)
                        const result = Module._DH(
                            ...graphAttributes,
                            ...arrays,
                            ...originalParameters
                        )
                        for (let i = 0; i < GRAPH_ATTRIBUTES[0]; ++i) {
                            _graph.nodes[i]['x'] = Module.HEAPF32[(result >> 2) + i * 2]
                            _graph.nodes[i]['y'] = Module.HEAPF32[(result >> 2) + i * 2 + 1]
                        }
                        callback(_graph)
                        ;(
                                    function(arrays, buffers) {
                                        for (let array of arrays) {
                                            Module._free(array)
                                        }
                                        for (let buffer of buffers) {
                                            Module._free_buf(buffer)
                                        }
                                    })(arrays, [result])
                    })
                }
            }
            dh.parameters = {"useWorker":{"type":"BOOL","range":[true,false],"default":false},"settings":{"type":"CATEGORICAL","range":["standard","repulse","planar"],"default":"none"},"attractionWeight":{"type":"DOUBLE","range":[0,null],"default":100},"iterationNumberAsFactor":{"type":"BOOL","range":[true,false],"default":false},"nodeOverlapWeight":{"type":"DOUBLE","range":[0,null],"default":100},"numberOfIterations":{"type":"INT","range":[0,null],"default":0},"planarityWeight":{"type":"DOUBLE","range":[0,null],"default":500},"preferredEdgeLength":{"type":"DOUBLE","range":[0,null],"default":0},"preferredEdgeLengthMultiplier":{"type":"DOUBLE","range":[0,null],"default":2},"repulsionWeight":{"type":"DOUBLE","range":[0,null],"default":1000000},"speed":{"type":"CATEGORICAL","range":["Fast","Medium","HQ"],"default":"Medium"},"startTemperature":{"type":"INT","range":[0,null],"default":500}}
            export default dh